<template>
  <div>
    <!-- 功能按钮 -->
    <div class="MT_fn_btn" v-if="queryInfo.queryType == 5">
      <el-form :inline="true" :model="queryInfo" class="btn_right demo-form-inline">
        <!-- <div class="tips">默认显示30天内数据</div> -->
        <el-form-item label="" prop="keywords">
          <el-date-picker size="small" @change="changeDate" v-model="queryInfo.chooseDate" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="MT_table_cont">
      <!-- 按钮 -->
      <div v-if="queryForm.type" class="btns">
        <div class="btn_cont">
          <div class="choose_btn _color">
            <span @click.stop="exportStand">标准数据导出</span>
          </div>
        </div>
      </div>
      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="title" label="类型"> </el-table-column>
        <el-table-column prop="count" :label="queryForm.type ? '个/次数' : '访问量'"> </el-table-column>
        <!-- 操作 -->
        <el-table-column v-if="!queryForm.type" label="操作" width="80">
          <template slot-scope="scope">
            <el-link :underline="false" type="primary" @click.stop="edit(scope.row.type)">查看</el-link>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryInfo.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryInfo, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryInfo, changePage)">尾页</span>
        <el-pagination
          background
          :total="queryInfo.total"
          :current-page.sync="queryInfo.page"
          :page-size.sync="queryInfo.size"
          @current-change="changePage"
          layout="total, prev, pager, next, jumper"
        />
      </div>
    </div>

    <!-- 详情对话框 -->
    <el-dialog title="访问记录" :visible.sync="dialogVisible" width="50%">
      <el-divider></el-divider>
      <!-- 按钮 -->
      <div class="btns">
        <div class="btn_cont">
          <div class="choose_btn _color">
            <span @click.stop="exportData">导出当前数据</span>
          </div>
        </div>
      </div>
      <el-table class="detail" :data="detailTable" style="width: 100%">
        <el-table-column prop="ipAddress" label="访问IP地址"> </el-table-column>
        <el-table-column prop="userName" label="访问用户名"> </el-table-column>
        <el-table-column prop="viewType" label="访问类型">
          <template slot-scope="scope">
            <span v-if="scope.row.viewType === -1">点击</span>
            <span v-if="scope.row.viewType === 0">浏览</span>
            <span v-if="scope.row.viewType === 1">登录</span>
          </template>
        </el-table-column>
        <el-table-column prop="viewTime" label="访问时间"> </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="query.total">
        <span class="jump_first" @click.stop="$jumpToFirst(query, loadData)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(query, loadData)">尾页</span>
        <el-pagination background :total="query.total" :current-page.sync="query.page" :page-size.sync="query.size" @current-change="loadData" layout="total, prev, pager, next, jumper" />
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ['dataInfo', 'queryForm'],
  data() {
    return {
      tableData: [],
      queryInfo: this.queryForm,
      dialogVisible: false,
      detailTable: [],
      query: {
        size: 20,
        page: 1,
        total: '',
        viewType: '',
        queryType: ''
      }
    }
  },

  methods: {
    // 搜索
    onSubmit() {
      this.$emit('getSearch', this.queryInfo)
    },

    // 页码改变
    changePage() {
      this.$emit('getPage', this.queryInfo.page)
    },

    edit(type) {
      this.dialogVisible = true
      this.query.viewType = type
      this.loadData()
    },

    changeDate(e) {
      if (!e) {
        this.queryInfo.startDate = ''
        this.queryInfo.endDate = ''
      } else {
        this.queryInfo.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryInfo.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      console.log(this.queryInfo)
    },

    loadData() {
      this.query.total = ''
      let params = Object.assign({}, this.query)
      params.queryType = this.queryInfo.queryType
      params.startDate = this.queryInfo.startDate
      params.endDate = this.queryInfo.endDate
      this.httpGet('/data/stats/visit/detail', params).then(res => {
        console.log(res)
        this.detailTable = res.rows
        this.query.total = res.total
      })
    },

    // 导出数据
    exportData() {
      let params = Object.assign({}, this.query)
      params.total = ''
      params.queryType = this.queryForm.queryType
      console.log(params)
      let paramArray = Object.keys(params).map(key => key + '=' + params[key])
      console.log(paramArray)
      let url = '/data/stats/visit/detail/export?'
      window.open(this.$baseRequestUrl + url + paramArray.join('&'))
    },

    // 导出数据
    exportStand() {
      let url = '/data/stats/standard/export'
      window.open(this.$baseRequestUrl + url)
    }
  },

  watch: {
    dataInfo(newVal, oldVal) {
      console.log(newVal)
      this.tableData = newVal
    },
    activeName(newVal, oldVal) {
      console.log(newVal)
    }
  },
  mounted() {}
}
</script>

<style lang="scss" scoped>
.btns {
  text-align: right;
  margin-bottom: 10px;
  span {
    cursor: pointer;
  }
}
.el-dialog {
  /deep/ .el-table td,
  .el-table th {
    padding: 0;
  }
}
</style>
