<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>数据统计</el-breadcrumb-item>
        <el-breadcrumb-item>标准统计</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="全部标准" name="total">
          <TableData v-if="activeName === 'total'" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" :activeName="activeName" />
        </el-tab-pane>
        <el-tab-pane label="国家标准" name="country">
          <TableData v-if="activeName === 'country'" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" :activeName="activeName" />
        </el-tab-pane>
        <el-tab-pane label="行业标准" name="industry">
          <TableData v-if="activeName === 'industry'" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" :activeName="activeName" />
        </el-tab-pane>
        <el-tab-pane label="地方标准" name="local">
          <TableData v-if="activeName === 'local'" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" :activeName="activeName" />
        </el-tab-pane>
        <el-tab-pane label="国际标准" name="internet">
          <TableData v-if="activeName === 'internet'" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" :activeName="activeName" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import TableData from './TableData'
export default {
  components: { TableData },
  data() {
    return {
      activeName: 'total',
      queryForm: {
        type: 1 //  1全部标准 2国家标准 3行业标准 4地方标准 5国际标准
      },
      dataInfo: []
    }
  },

  methods: {
    getPage(e) {
      this.queryForm.page = e
      this.loadData()
    },

    getSearch(e) {
      this.queryForm = e
      this.loadData()
    },

    loadData() {
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate
      this.httpGet('/data/stats/standard/' + this.queryForm.type, {}, '', 1).then(res => {
        console.log(res)
        let data = [
          { title: '题录个数', count: res.totalCount },
          { title: '现行个数', count: res.useCount },
          { title: '包含文本个数', count: res.fileCount },
          { title: '标准下载个数', count: res.downloadCount },
          { title: '标准下载次数', count: res.downloadNumber }
        ]
        this.dataInfo = data
      })
    },

    handleClick(tab, event) {
      console.log(this.activeName)
      switch (this.activeName) {
        case 'total':
          this.queryForm.type = 1
          break
        case 'country':
          this.queryForm.type = 2
          break
        case 'industry':
          this.queryForm.type = 3
          break
        case 'local':
          this.queryForm.type = 4
          break
        default:
          this.queryForm.type = 5
          break
      }
      this.loadData()
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
