<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>数据统计</el-breadcrumb-item>
        <el-breadcrumb-item>访问统计</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="一周访问量" name="week">
          <TableData v-if="activeName === 'week'" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" :activeName="activeName" />
        </el-tab-pane>
        <el-tab-pane label="30天访问量" name="month">
          <TableData v-if="activeName === 'month'" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" :activeName="activeName" />
        </el-tab-pane>
        <el-tab-pane label="本年访问量" name="year">
          <TableData v-if="activeName === 'year'" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" :activeName="activeName" />
        </el-tab-pane>
        <el-tab-pane label="总访问量" name="total">
          <TableData v-if="activeName === 'total'" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" :activeName="activeName" />
        </el-tab-pane>
        <el-tab-pane label="日期统计" name="date">
          <TableData v-if="activeName === 'date'" :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getSearch="getSearch" :activeName="activeName" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import TableData from './TableData'
export default {
  components: { TableData },
  data() {
    return {
      activeName: 'week',
      queryForm: {
        chooseDate: '',
        startDate: '',
        endDate: '',
        queryType: 1 //  1一周访问量 230天访问量 3本年访问量 4总访问量 5日期统计
      },
      dataInfo: []
    }
  },

  methods: {
    getPage(e) {
      this.queryForm.page = e
      this.loadData()
    },

    getSearch(e) {
      this.queryForm = e
      this.loadData()
    },

    loadData() {
      let params = Object.assign({}, this.queryForm)
      delete params.chooseDate
      this.httpGet('/data/stats/visit', params, '', 1).then(res => {
        console.log(res)
        let data = [
          { title: '点击量', count: res.clickCount, type: -1 },
          { title: '总访问量', count: res.viewCount, type: 0 },
          { title: '用户访问量', count: res.keyViewCount, type: 1 }
        ]
        this.dataInfo = data
      })
    },
    
    handleClick(tab, event) {
      console.log(this.activeName)
      switch (this.activeName) {
        case 'week':
          this.queryForm.queryType = 1
          break
        case 'month':
          this.queryForm.queryType = 2
          break
        case 'year':
          this.queryForm.queryType = 3
          break
        case 'total':
          this.queryForm.queryType = 4
          break
        default:
          this.queryForm.queryType = 5
          break
      }
      this.loadData()
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
